import Login from "../pages/Login/Login";
import Nominated from "../pages/Nominated/Nominated";
import Search from "../pages/Search/Search";
import Signup from "../pages/Signup/Signup";

const routes = [
  {
    path: "/login",
    component: Login,
    isPrivate: false,
  },
  {
    path: "/signup",
    component: Signup,
    isPrivate: false,
  },
  {
    path: "/nominated",
    component: Nominated,
    isPrivate: true,
  },
  {
    path: "/search",
    component: Search,
    isPrivate: true,
  },
  {
    path: "/",
    component: Login,
    isPrivate: false,
  },
];

export default routes;
